body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.list-group-item:hover {
  background-color: #f5f5f5;
  cursor: pointer;
}

.chatListDiv {
  overflow: auto; 
  display: -ms-flexbox; 
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
}

.chatListContainer {
  overflow-y: auto;
  position: fixed;
  width: calc(33.33% - 30px - 30px - 30px);
  height: calc(100% - 190px - 100px - 60px - 30px) !important;
  margin: auto;
}

.JoinChatroom {
  height: 100%;
}

.chatroomListPanel {
  height: calc(100% - 190px - 100px) !important;
}


